import dayjs from 'dayjs';
export default {
    name: 'Footer',
    data() {
        return {
            date: {
                day: 0,
                hour: 0,
                minute: 0,
                second: 0
            }
        };
    },
    mounted() {
        const startDate = dayjs('2016/12/12');
        setInterval(() => {
            const time = dayjs();
            const day = time.diff(startDate, 'day');
            const hour = time.get('hour');
            const minute = time.get('minute');
            const second = time.get('second');
            this.date = {
                day,
                hour,
                minute,
                second
            };
        }, 1000);
    },
    methods: {}
};
