export default {
    data: () => {
        return {
            inTop: true
        };
    },
    mounted() {
        this.handleScroll();
    },
    methods: {
        handleScroll() {
            if (process.client) {
                window.addEventListener(
                    'scroll',
                    () => {
                        const element = document.querySelector('.back-top');
                        if (!element) return false;
                        if (window.scrollY > 1000 && this.inTop) {
                            element.classList.add('x');
                        } else {
                            element.classList.remove('x');
                        }
                    },
                    {
                        passive: true
                    }
                );
            }
        },
        backTop() {
            if (this.inTop) {
                this.inTop = false; // 阻止多次點擊返回頂部
                this.$store.dispatch('jump').then((e) => {
                    this.inTop = true;
                });
            }
        }
    }
};
